export default{
	data(){
		return {
			hobbit:'',
			itemList:['唱歌','跳舞','喝茶']
		}
	},
	methods:{
		choose(){
			var _self=this
			uni.showActionSheet({
				itemList:['唱歌','跳舞','喝茶'],
				success(res) {
					_self.hobbit=_self.itemList[res.tapIndex]
					// _self.hobbit=res.
				}
			})
		}
	}
}
